import { useTranslation } from "react-i18next";
import { useState } from "react";
import ModalMenu from "../../../../../optimized/Ui/Modal/ModalMenu";
import { useSubjectRemoveCatalogStudent } from "../../../../../optimized/Hooks/ApiHooks/SubjectManager/subject-manager-api";

const SubjectCatalogMenu = ({ sid, onClose, menuData, onRefetch }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);

  const [subjectRemoveCatalogStudent] = useSubjectRemoveCatalogStudent();

  const onDelete = () => {
    if (menuData?._id && sid) {
      setDisabled((pre) => !pre);
      subjectRemoveCatalogStudent({
        sid: sid,
        bodyParameter: {
          student_list: [menuData?._id],
          isBatch: true,
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };

  return (
    <>
      <ModalMenu onClose={onClose} disabled={disabled}>
        <h6 onClick={onDelete}>{t("remove")}</h6>
      </ModalMenu>
    </>
  );
};

export default SubjectCatalogMenu;
