import React, { useState } from "react";
import { useTranslation } from "react-i18next";
import {
  useSubjectStudentAttendance,
  useSubjectStudentAttendanceUpdate,
} from "../../../../../hooks/member_tab/subject-api";
import QLoader from "../../../../../Loader/QLoader";
import ExportStudentSubjectAttendance from "../../../../../Department/Institute/Components/exportData/ExportStudentSubjectAttendance";
import { useDispatch } from "react-redux";
import { attendanceStoreAction } from "../../../../../redux/store/attendance-slice";
import { Notfication } from "../../../../../validation/Snackbar";
import { useLocation, useNavigate, useParams } from "react-router-dom";
import CustomCreateButton from "../../../../../optimized/Ui/Button/CustomCreateButton";
import AddAcademicRecord from "../Academics/AddAdcemicRecord/AddAcademicRecord";
import AttendaceTimeSlotLogs from "./TimeSlot/AttendaceTimeSlotLogs";

const SaveButton = ({
  today,
  alreadyMark,
  allIdsArray,
  // subjectStudentCurrentAttendanceRefetch,
  clickedIndex,
  setClickedIndex,
  idPresentArray,
  idAbsentArray,
  setIdPresentArray,
  setIdAbsentArray,
  sid,
  isDepartment,
  subjectName,
  setIsRefetch,
  onExtraAction,
  attendaceView,
  timeSlot,
  setTimeSlot,
  whichLecture,
  isNewUi,
  cid,
}) => {
  // const [showSave, setShowSave] = React.useState(false);
  const [disabled, setDisabled] = React.useState(false);
  const [addRecord, setAddRecord] = React.useState(false);
  const [exportAttendance, setExportAttendance] = useState(false);
  const [subjectStudentAttendance] = useSubjectStudentAttendance();
  const [subjectStudentAttendanceUpdate] = useSubjectStudentAttendanceUpdate();
  const [logs, setLogs] = useState(false);

  const { t } = useTranslation();
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const params = useParams();
  const getQuery = useLocation();
  const [notificationState, setNotificationState] = useState({
    msg: "",
    run: false,
  });
  const [extra, setExtra] = useState("");

  const dt = today.split("-");
  const d = dt[0] > 9 ? dt[0] : `0${dt[0]}`;
  const mt = dt[1] > 9 ? dt[1] : `0${dt[1]}`;

  // React.useEffect(() => {
  //   if (idPresentArray?.length || idAbsentArray?.length) {
  //     setShowSave(true);
  //   } else {
  //     setShowSave(false);
  //   }
  // }, [idPresentArray, idAbsentArray]);
  const onRecordToggle = () => {
    setAddRecord((pre) => !pre);
  };
  const submitHandler = (flow) => {
    // onRecordToggle();

    if (idPresentArray?.length || idAbsentArray?.length) {
      setDisabled((pre) => !pre);
      const dates = today.split("-");
      const day = dates[0] > 9 ? dates[0] : `0${dates[0]}`;
      const month = dates[1] > 9 ? dates[1] : `0${dates[1]}`;
      if (!alreadyMark?._id) {
        if (timeSlot?.from && timeSlot?.to) {
          const idArray = [];
          allIdsArray?.map((val) => idArray.push(val._id));
          const present = [];
          const absent = [];

          if (idPresentArray?.length) {
            for (let id of idArray) {
              let count1 = 0;
              if (idPresentArray?.includes(id)) {
              } else absent.push(id);
              count1 = count1 + 1;
            }
          } else {
            for (let id of idArray) {
              let count2 = 0;
              if (idAbsentArray?.includes(id)) {
              } else present.push(id);
              count2 = count2 + 1;
            }
          }
          if (timeSlot?.already_slot_mark) {
            setNotificationState({
              msg: `For this time slot attendace has been already marked by ${
                timeSlot?.subject?.subjectName
              } - ${`${
                timeSlot?.subject?.subjectTeacherName?.staffFirstName ?? ""
              } ${
                timeSlot?.subject?.subjectTeacherName?.staffMiddleName ?? ""
              } ${
                timeSlot?.subject?.subjectTeacherName?.staffLastName ?? ""
              }`}`,
              run: true,
            });
          } else {
            subjectStudentAttendance({
              sid: sid,
              flow: flow,
              from: timeSlot?.from
                ? timeFormatTable(timeSlot?.from)
                : getTimeConvert(),
              to: timeSlot?.to
                ? timeFormatTable(timeSlot?.to)
                : getTimeConvert(),
              attendance: {
                date: `${day}/${month}/${dates[2]}`,
                present: idPresentArray?.length ? idPresentArray : present,
                absent: idAbsentArray?.length ? idAbsentArray : absent,
                from: timeSlot?.from
                  ? timeFormatTable(timeSlot?.from)
                  : getTimeConvert(),
                to: timeSlot?.to
                  ? timeFormatTable(timeSlot?.to)
                  : getTimeConvert(),
                which_lecture: whichLecture,
              },
            })
              .then((res) => {
                if (res?.data?.already_slot_mark) {
                  setNotificationState({
                    msg: `For this time slot attendace has been already marked by ${
                      res?.data?.subject?.subjectName
                    } - ${`${
                      res?.data?.subject?.subjectTeacherName?.staffFirstName ??
                      ""
                    } ${
                      res?.data?.subject?.subjectTeacherName?.staffMiddleName ??
                      ""
                    } ${
                      res?.data?.subject?.subjectTeacherName?.staffLastName ??
                      ""
                    }`}`,
                    run: true,
                  });
                } else if (
                  res?.data?.message ===
                  "Today will be holiday Provided by department Admin or already marked attendance"
                ) {
                  setNotificationState({
                    msg: "Today will be holiday Provided by department Admin or already marked attendance",
                    run: true,
                  });
                } else {
                  dispatch(
                    attendanceStoreAction.addSubjectAttendance({
                      subjectId: sid,
                      ldate: `${day}/${month}/${dates[2]}`,
                      presentStudent: idPresentArray?.length
                        ? idPresentArray
                        : present,
                      absentStudent: idAbsentArray?.length
                        ? idAbsentArray
                        : absent,
                      alreadyMark: res?.data?.alreadyMark,
                    })
                  );
                  onRecordToggle();
                }

                // subjectStudentCurrentAttendanceRefetch();
                setClickedIndex(0);
                setDisabled((pre) => !pre);
                setIdPresentArray([]);
                setIdAbsentArray([]);
                // if (flow === "Extra_Lecture") navigate(-1);
                if (flow === "Extra_Lecture") {
                  setExtra(res?.data?.alreadyMark);
                }
                setTimeSlot((prev) => ({
                  ...prev,
                  is_mark: true,
                  is_timetable: false,
                }));
              })
              .catch({});
          }
        } else {
          setDisabled((pre) => !pre);
        }
      } else {
        dispatch(
          attendanceStoreAction.updateSubjectAttendance({
            subjectId: sid,
            ldate: `${day}/${month}/${dates[2]}`,
            presentStudent: idPresentArray?.length ? idPresentArray : [],
            absentStudent: idAbsentArray?.length ? idAbsentArray : [],
            alreadyMark: alreadyMark?._id,
          })
        );
        subjectStudentAttendanceUpdate({
          said: alreadyMark?._id,
          flow: flow,
          attendanceUpdate: {
            date: `${day}/${month}/${dates[2]}`,
            present: idPresentArray?.length ? idPresentArray : [],
            absent: idAbsentArray?.length ? idAbsentArray : [],
          },
        })
          .then(() => {
            setIsRefetch(true);
            // subjectStudentCurrentAttendanceRefetch();
            setClickedIndex(0);
            setDisabled((pre) => !pre);
            setIdPresentArray([]);
            setIdAbsentArray([]);
          })
          .catch({});
      }
    }
  };
  const onCloseAttendance = () => {
    setExportAttendance((pre) => !pre);
  };

  const onSkipAcademic = () => {
    navigate(
      `/q/${params.username}/member/subject/${params.sname}?a=assignment&type=create`,
      {
        state: { ...getQuery.state, isAttendance: true },
      }
    );
  };

  return (
    <>
      {isNewUi ? (
        <>
          {clickedIndex !== 0
            ? !isDepartment &&
              (attendaceView === "EXTRA_MARK" ? (
                <CustomCreateButton
                  label={t("save_attendance")}
                  onAction={() => submitHandler("Extra_Lecture")}
                  customStyle={{
                    "--ccb-bg": "#436DFA",
                    "--ccb-b": "#436DFA",
                    "--ccb-text": "#ffffff",
                  }}
                />
              ) : (
                <CustomCreateButton
                  label={t("save_attendance")}
                  onAction={() => submitHandler("Normal_Lecture")}
                  customStyle={{
                    "--ccb-bg": "#436DFA",
                    "--ccb-b": "#436DFA",
                    "--ccb-text": "#ffffff",
                  }}
                />
              ))
            : ""}
        </>
      ) : (
        <div
          style={{
            display: "flex",
            justifyContent: "flex-start",
            alignItems: "center",
            gap: "1.5rem",
          }}
        >
          {attendaceView === "EXTRA_MARK" ? null : (
            <>
              <CustomCreateButton
                label={t("export")}
                onAction={onCloseAttendance}
                customStyle={{
                  "--ccb-bg": "#436DFA",
                  "--ccb-b": "#436DFA",
                  "--ccb-text": "#ffffff",
                  width: "fit-content",
                  padding: "0.2rem 1rem",
                  fontWeight: "400",
                  fontSize: "14px",
                }}
              />
              {!isDepartment && (
                <CustomCreateButton
                  label={t("extra_lecture")}
                  onAction={onExtraAction}
                  customStyle={{
                    "--ccb-bg": "#436DFA",
                    "--ccb-b": "#436DFA",
                    "--ccb-text": "#ffffff",
                    width: "fit-content",
                    padding: "0.2rem 1rem",
                    fontWeight: "400",
                    fontSize: "14px",
                  }}
                />
              )}
              <CustomCreateButton
                label={t("time_logs")}
                onAction={() => setLogs(true)}
                customStyle={{
                  "--ccb-bg": "#436DFA",
                  "--ccb-b": "#436DFA",
                  "--ccb-text": "#ffffff",
                  width: "fit-content",
                  padding: "0.2rem 1rem",
                  fontWeight: "400",
                  fontSize: "14px",
                }}
              />
            </>
          )}
        </div>
      )}

      {disabled && <QLoader />}
      {exportAttendance && (
        <ExportStudentSubjectAttendance
          sid={sid}
          onClose={onCloseAttendance}
          className={subjectName}
        />
      )}
      {addRecord && (
        <AddAcademicRecord
          onClose={onRecordToggle}
          sid={sid}
          onRefetch={() => {}}
          isAttendance
          date={today}
          extra={extra}
          onSkip={onSkipAcademic}
        />
      )}
      {logs && (
        <AttendaceTimeSlotLogs
          cid={cid}
          sid={sid}
          onClose={() => setLogs(false)}
          today={`${d}/${mt}/${dt[2]}`}
        />
      )}
      {notificationState.run && (
        <Notfication
          msg={notificationState.msg}
          run={notificationState.run}
          setRun={() => setNotificationState({ msg: "", run: false })}
          postiton="bottom"
          type="error"
        />
      )}
    </>
  );
};

export default SaveButton;

const getTimeConvert = () => {
  const time = new Date();
  let hr = time.getHours();
  let mm = time.getMinutes();
  let a = "";
  if (hr > 12) {
    let mt = hr - 12;
    hr = mt > 9 ? mt : `0${mt}`;
    a = "Pm";
  } else if (hr === 12) {
    a = "Pm";
  } else {
    a = "Am";
  }
  return `${hr}:${mm} ${a}`;
};
const timeFormatTable = (vale) => {
  const splitValue = vale?.split(":");
  let meridian = "";
  if (splitValue[0] > 12) {
    meridian = "Pm";
    splitValue[0] -= 12;
  } else if (splitValue[0] < 12) {
    meridian = "Am";
    if (splitValue[0] === 0) {
      splitValue[0] = 12;
    }
  } else {
    meridian = "Pm";
  }
  const hour = splitValue[0] > 9 ? splitValue[0] : `0${+splitValue[0]}`;
  let res = `${hour}:${splitValue[1]} ${meridian}`;
  return res?.substring(0, 8);
};

