import {
  assestsFeedUrl,
  assestsUserAdmissionUrl,
} from "../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../../PlateformUser/PlateformUser.module.css";
import { imageShowUrl } from "../../../Services/UrlConfig/BaseUrl";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import { useTranslation } from "react-i18next";
const SelectSubjectCatalogCard = ({
  student,
  selectedId,
  onSelect,
  selectList,
  isMultiSelect,
  onMultiSelect,
}) => {
  const { t } = useTranslation();
  const onSelectStudent = () => {
    onSelect(student);
  };
  const onSelectMultipleStudent = () => {
    onMultiSelect(student);
  };
  return (
    <>
      {isMultiSelect ? (
        <div
          className={style.assign_show_list_each}
          onClick={onSelectMultipleStudent}
        >
          {selectList?.includes(student?._id) ? (
            <img
              src={`${assestsUserAdmissionUrl}/check.svg`}
              alt="icon"
              style={{
                height: "1.3rem",
                borderRadius: "0",
                cursor: "pointer",
              }}
            />
          ) : (
            <img
              style={{
                height: "1.3rem",
                borderRadius: "0",
                cursor: "pointer",
              }}
              src={`${assestsUserAdmissionUrl}/uncheck.svg`}
              alt="icon"
            />
          )}
          <img
            src={
              student?.studentProfilePhoto
                ? `${imageShowUrl}/${student.studentProfilePhoto}`
                : `${assestsFeedUrl}/feed-staff-avatar.svg`
            }
            alt="profile avatar"
          />
          <div className={style.assign_each_paragraph}>
            <h6>{`${student?.studentFirstName ?? ""} ${
              student?.studentMiddleName ?? ""
            } ${student?.studentLastName ?? ""}`}</h6>
            <p>
              {t("gr_number")} {" : "}
              {student?.studentGRNO}
            </p>
          </div>
        </div>
      ) : (
        <div
          className={style.assign_show_list_each}
          style={
            selectedId === student?._id
              ? { backgroundColor: "rgba(75, 26, 133, 0.1)" }
              : null
          }
          onClick={onSelectStudent}
        >
          <img
            src={
              student?.studentProfilePhoto
                ? `${imageShowUrl}/${student.studentProfilePhoto}`
                : `${assestsFeedUrl}/feed-staff-avatar.svg`
            }
            alt="profile avatar"
          />
          <div className={style.assign_each_paragraph}>
            <h6>{`${student?.studentFirstName ?? ""} ${
              student?.studentMiddleName ?? ""
            } ${student?.studentLastName ?? ""}`}</h6>
            <p>
              {t("gr_number")} {" : "}
              {student?.studentGRNO}
            </p>
          </div>
        </div>
      )}

      <BorderBottom
        customStyleBorder={{
          margin: "0",
        }}
      />
    </>
  );
};

export default SelectSubjectCatalogCard;
