import { useEffect, useState } from "react";
import {
  useSubjectDayAttendanceAddLecture,
  useSubjectDayAttendanceLecture,
} from "../../../../../../hooks/member_tab/subject-api";
import QLoader from "../../../../../../Loader/QLoader";
import style from "../../../ClassTeacher/ClassCatalog/Attendence/Attendence.module.css";
import { assestsUtilityUrl } from "../../../../../../optimized/Services/UrlConfig/AssestsBaseUrl";
import dayjs from "dayjs";
import { useLocation } from "react-router-dom";

const AttendanceDayLecture = ({
  sid,
  today,
  whichLecture,
  setWhichLecture,
}) => {
  const getQuery = useLocation();
  const dt = today.split("-");
  const d = dt[0] > 9 ? dt[0] : `0${dt[0]}`;
  const mt = dt[1] > 9 ? dt[1] : `0${dt[1]}`;
  let date = `${d}/${mt}/${dt[2]}`;
  const [disabled, setDisabled] = useState(false);

  const [subjectDayAttendanceAddLecture] = useSubjectDayAttendanceAddLecture();

  const { subjectDayAttendanceLecture, subjectDayAttendanceLectureRefetch } =
    useSubjectDayAttendanceLecture({
      data: {
        date: date,
        sid: sid,
        which_day: dayjs().format("dddd"),
        // date: "11/03/2024",
        // which_day: "Monday",
      },
      skip: !sid ? !sid : !date,
    });

  useEffect(() => {
    if (sid && today) {
      subjectDayAttendanceLectureRefetch();
    }
  }, [sid, today, subjectDayAttendanceLectureRefetch]);

  useEffect(() => {
    if (getQuery.state?.dashboardWhichLecture) {
      setWhichLecture(getQuery.state?.dashboardWhichLecture);
    }
  }, [getQuery.state?.dashboardWhichLecture]);
  const onAddLecture = () => {
    if (sid && today && subjectDayAttendanceLecture?.day_lecture?.length >= 0) {
      let lt = 1;
      if (
        subjectDayAttendanceLecture?.day_lecture?.[0]?.which_lecture === "1"
      ) {
        lt = subjectDayAttendanceLecture?.day_lecture?.length + 1;
      } else {
        lt = subjectDayAttendanceLecture?.day_lecture?.length + 2;
      }
      setDisabled((pre) => !pre);
      subjectDayAttendanceAddLecture({
        sid: sid,
        timeSlot: {
          which_lecture: `${lt}`,
          date: date,
        },
      })
        .then(() => {
          subjectDayAttendanceLectureRefetch();
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  // console.info(
  //   "subjectDayAttendanceLecture",
  //   getQuery.state?.dashboardWhichLecture
  // );
  return (
    <>
      <div className={style.al_container}>
        {subjectDayAttendanceLecture?.day_lecture?.length > 0 ? (
          <>
            {subjectDayAttendanceLecture?.day_lecture?.[0]?.which_lecture ===
            "1" ? null : (
              <h6
                onClick={() => setWhichLecture("1")}
                className={
                  whichLecture === "1"
                    ? `${style.al_container_active} ${style.al_container_h6}`
                    : style.al_container_h6
                }
              >
                1st
              </h6>
            )}

            {subjectDayAttendanceLecture?.day_lecture?.map((lt) => (
              <h6
                key={lt?.which_lecture}
                onClick={() => setWhichLecture(lt.which_lecture)}
                className={
                  whichLecture === lt.which_lecture
                    ? `${style.al_container_active} ${style.al_container_h6}`
                    : style.al_container_h6
                }
              >
                {lectureObj[lt.which_lecture]}
              </h6>
            ))}
          </>
        ) : null}

        <img
          src={`${assestsUtilityUrl}/add.svg`}
          alt="add button"
          onClick={onAddLecture}
          className={style.al_container_img}
        />
      </div>
      {disabled && <QLoader />}
    </>
  );
};

export default AttendanceDayLecture;

const lectureObj = {
  1: "1st",
  2: "2nd",
  3: "3rd",
  4: "4th",
  5: "5th",
  6: "6th",
  7: "7th",
  8: "8th",
};
