import { useTranslation } from "react-i18next";
import style from "./Button.module.css";
const CustomCreateButton = ({
  label,
  count,
  onAction,
  customStyle,
  isTranslate,
  showZero,
  children,
}) => {
  const { t } = useTranslation();

  return isTranslate ? (
    <div className={style.ccb_button} onClick={onAction} style={customStyle}>
      {children}
      {showZero ? (
        <span
          style={{
            marginRight: "0.3rem",
          }}
        >
          {count}
        </span>
      ) : count > 0 ? (
        <span
          style={{
            marginRight: "0.3rem",
          }}
        >
          {count}{" "}
        </span>
      ) : null}
      {label}
    </div>
  ) : (
    <div className={style.ccb_button} onClick={onAction} style={customStyle}>
      {children}
      {showZero ? (
        <span
          style={{
            marginRight: "0.3rem",
          }}
        >
          {count}
        </span>
      ) : count > 0 ? (
        <span
          style={{
            marginRight: "0.3rem",
          }}
        >
          {count}{" "}
        </span>
      ) : null}
      {t(label)}
    </div>
  );
};

export default CustomCreateButton;
