import { t } from "i18next";
import React, { useEffect, useState } from "react";
import {
  useSubjectMarkExcelExport,
  useSubjectMarkExcelImport,
  useSubjectOneExamMarks,
  useSubjectOneExamStudents,
} from "../../../../../hooks/member_tab/subject-api";
import QLoader from "../../../../../Loader/QLoader";
import { imageShowUrl } from "../../../../../services/BaseUrl";
import style from "./Markdetails.module.css";
import { Notfication } from "../../../../../validation/Snackbar";
import CreateButton from "../../../../../optimized/Ui/Button/CreateButton";
import UploadExcelButton from "../../../../../optimized/Ui/Button/UploadExcelButton";
import { pdfCdnUrl } from "../../../../../optimized/Services/UrlConfig/BaseUrl";
import { useFileUpload } from "../../../../../hooks/authentication-api";

function MarkDetails({ examView, sid, isDepartment, cid }) {
  const [subjects, setSubjects] = useState([]);
  const [isChange, setIsChange] = useState(false);
  const [disabled, setDisabled] = useState(true);
  const [notificationState, setNotificationState] = useState({
    msg: "",
    run: false,
    isSuccess: false,
  });
  const { subjectOneExamStudents, subjectOneExamStudentsRefetch } =
    useSubjectOneExamStudents({
      data: { sid: sid, eid: examView?._id, page: 1, limit: 50, cid: cid },
      skip: sid ? !cid : !sid,
    });
  const [subjectOneExamMarks] = useSubjectOneExamMarks();

  const [fileUpload] = useFileUpload();
  const [subjectMarkExcelExport] = useSubjectMarkExcelExport();
  const [subjectMarkExcelImport] = useSubjectMarkExcelImport();

  useEffect(() => {
    if (sid) subjectOneExamStudentsRefetch();
  }, [sid, subjectOneExamStudentsRefetch]);

  useEffect(() => {
    if (subjectOneExamStudents?.students) setDisabled(false);
  }, [subjectOneExamStudents?.students]);

  useEffect(() => {
    if (subjectOneExamStudents?.students?.length > 0) {
      let arr = [];
      for (let st of subjectOneExamStudents?.students) {
        arr.push({
          studentId: st?._id,
          obtainMarks: st?.obtainMarks ?? 0,
        });
      }
      setSubjects(arr);
    }
  }, [subjectOneExamStudents?.students]);

  const onInputClick = (val, e) => {
    if (isDepartment) {
    } else {
      if (subjects?.length) {
        const allIds = [];
        for (let sub of subjects) {
          allIds.push(sub.studentId);
        }
        if (!allIds?.includes(val)) {
          setSubjects([
            ...subjects,
            {
              studentId: val,
              obtainMarks: 0,
            },
          ]);
        }
      } else {
        setSubjects([
          ...subjects,
          {
            studentId: val,
            obtainMarks: 0,
          },
        ]);
      }
      setIsChange(true);
      // console.info("key traget", e?.key);
    }
  };

  const onInputChange = (e) => {
    if (isDepartment) {
    } else {
      if (+e.target.value < 0) {
        setNotificationState({
          msg: t("obtain_marks_validation_2"),
          run: true,
        });
      } else if (+e.target.value <= +examView?.totalMarks) {
      } else {
        setNotificationState({
          msg: `${t("obtain_marks_validation_1")} ${examView?.totalMarks}`,
          run: true,
        });
      }
      for (let sub of subjects) {
        if (sub.studentId === e.target.name) {
          sub.obtainMarks = parseInt(e.target.value);
        }
      }
      // console.info("key traget", e?.key);
    }
  };

  // const onKeyNextTarget = (e, ind) => {
  //   console.info("key upp", e, ind);
  //   if(e?.key ==="Enter" || e?.keyCode ===13){

  //   }
  // };
  const onSubmitMarks = () => {
    if (isDepartment) {
    } else {
      let flag = true;
      for (let mark of subjects) {
        if (mark?.obtainMarks < 0) {
          setNotificationState({
            msg: t("obtain_marks_validation_2"),
            run: true,
          });
          flag = false;
          break;
        } else if (mark?.obtainMarks > +examView?.totalMarks) {
          setNotificationState({
            msg: `${t("obtain_marks_validation_1")} ${examView?.totalMarks}`,
            run: true,
          });
          flag = false;
          break;
        } else {
        }
      }
      if (flag) {
        setDisabled((pre) => !pre);
        subjectOneExamMarks({
          sid: sid,
          marksList: {
            examId: examView?._id,
            marks: subjects,
          },
        })
          .then(() => {
            subjectOneExamStudentsRefetch();
            setDisabled((pre) => !pre);
            setNotificationState({
              msg: t("obtain_marks_validation_3"),
              run: true,
              isSuccess: true,
            });
            setIsChange(false);
          })
          .catch({});
      }
    }
  };

  const onExcelFileUpload = (e) => {
    if (e.target.files?.length) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", e.target.files[0]);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          if (res.data?.imageKey && sid && examView?._id && cid) {
            subjectMarkExcelImport({
              sid: sid,
              bodyParameter: {
                examId: examView?._id,
                cid: cid,
                excel_key: res.data?.imageKey,
              },
            })
              .then(() => {
                setDisabled((pre) => !pre);
              })
              .catch({});
          } else {
            setDisabled((pre) => !pre);
          }
        })
        .catch({});
    }
  };

  const onExport = () => {
    if (sid && cid && examView?._id) {
      setDisabled((pre) => !pre);
      subjectMarkExcelExport({
        sid: sid,
        bodyParameter: {
          eid: examView?._id,
          cid: cid,
        },
      })
        .then((res) => {
          if (res?.data?.excel_key) {
            setDisabled((pre) => !pre);
            const newWindow = window.open(
              `${pdfCdnUrl}/${res?.data?.excel_key}`,
              "_blank",
              "noopener,noreferrer"
            );
            if (newWindow) newWindow.opener = null;
          } else {
            setDisabled((pre) => !pre);
          }
        })
        .catch({});
    }
  };

  // console.info("dsfgdgd", subjects);
  return (
    <>
      <div className={style.markDetails}>
        <div className={style.markDetailsHeader}>
          <h6>
            <span className={style.span}>{t("exam")} : </span>{" "}
            {examView?.examName}
          </h6>
          <div className={style.md_container}>
            {!isDepartment ? (
              <>
                <UploadExcelButton
                  onExcelFileUpload={onExcelFileUpload}
                  label={t("import_marks_excel")}
                  customStyleLabel={{
                    fontSize: "12px",
                  }}
                />
                <CreateButton
                  label="export"
                  customStyle={{
                    paddingBlock: "0.2rem",
                    margin: "0",
                  }}
                  onAction={onExport}
                />
                {isChange ? (
                  <div className={style.savebtn} onClick={onSubmitMarks}>
                    {t("save")}
                  </div>
                ) : (
                  ""
                )}
              </>
            ) : null}
          </div>
        </div>
        <div className={style.markDetailsHeader2}>
          <h6>
            <span className={style.span2}> {t("Maximum Marks")} : </span>{" "}
            {examView?.totalMarks}
          </h6>
          {/* {examView?.examType === "Final" ? (
          ""
        ) : (
          <h6>
            <span className={style.span2}> {t("weightage")} : </span>{" "}
            {examView?.examWeight}%
          </h6>
        )} */}
        </div>

        <div className={style.table}>
          <table>
            <thead className={style.thead}>
              <tr className={style.tr}>
                <th className={`${style.th} ${style.rroll}`}>{t("roll_no")}</th>
                <th
                  className={`${style.th} ${style.rname}`}
                  style={{
                    width: "50%",
                  }}
                >
                  {t("name")}
                </th>
                <th
                  className={`${style.th}`}
                  style={{
                    width: "20%",
                    textAlign: "center",
                  }}
                >
                  {t("obtain_marks")}
                </th>
                <th
                  className={`${style.th}`}
                  style={{
                    width: "10%",
                    textAlign: "center",
                  }}
                >
                  {t("attendance")}
                </th>
              </tr>
            </thead>
            <tbody className={style.tbody}>
              {subjectOneExamStudents?.students?.map((stu, index) => (
                <tr className={style.tr} key={stu?._id}>
                  <td className={`${style.rroll} ${style.rrolld} ${style.td}`}>
                    {stu?.studentROLLNO !== "" ? (
                      <h6> {stu?.studentROLLNO}.</h6>
                    ) : (
                      <h6></h6>
                    )}
                  </td>
                  <td className={style.td}>
                    <div className={style.student}>
                      <img
                        src={
                          stu?.studentProfilePhoto
                            ? `${imageShowUrl}/${stu?.studentProfilePhoto}`
                            : "/images/member_tab/class/default_avatar.svg"
                        }
                        alt=""
                      />
                      <h6>
                        {stu?.studentFirstName}{" "}
                        {stu?.studentMiddleName ? stu?.studentMiddleName : " "}{" "}
                        {stu?.studentLastName}
                      </h6>
                    </div>
                  </td>
                  <td
                    className={`${style.td} ${style.tdinputcontaier}`}
                    style={{
                      width: "20%",
                      textAlign: "center",
                    }}
                  >
                    <div className={style.tdinput}>
                      <input
                        type="tel"
                        name={stu?._id}
                        defaultValue={
                          stu?.obtainMarks === 0 ? "" : stu?.obtainMarks
                        }
                        // value={}
                        placeholder="0"
                        onClick={(e) => onInputClick(stu?._id, e)}
                        onChange={onInputChange}
                        // onKeyUp={(e) => onKeyNextTarget(e, index)}
                      />
                    </div>
                  </td>
                  <td
                    className={style.td}
                    style={{
                      width: "10%",
                      textAlign: "center",
                    }}
                  >
                    {stu.present === null ? "_" : stu.present ? "P" : "A"}
                    {/* {(
                    (stu.obtainMarks * examView?.examWeight) /
                    examView?.totalMarks
                  ).toFixed(2)} */}
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>
      {disabled && <QLoader />}
      <Notfication
        msg={notificationState.msg}
        run={notificationState.run}
        setRun={() => setNotificationState({ msg: "", run: false })}
        postiton="bottom"
        type={notificationState.isSuccess ? "success" : "error"}
      />
    </>
  );
}

export default MarkDetails;
