import PopupWrapper from "../../../FinanceManager/FeesStructure/PopupWrapper";
import style from "../../Academics/Academics.module.css";
import { useTranslation } from "react-i18next";
import BorderBottom from "../../../../../../StaffTab/components/DividerTab/MainContent/StaffAProfile/BorderBottom";
import { useSubjectAlreadySlotMarkList } from "../../../../../../hooks/member_tab/subject-api";
import { useEffect } from "react";
import { assestsFeedUrl } from "../../../../../../optimized/Services/UrlConfig/AssestsBaseUrl";
import { imageShowUrl } from "../../../../../../services/BaseUrl";
import EmptyMessage from "../../../../../../optimized/Utils/EmptyMessage/EmptyMessage";

const AttendaceTimeSlotLogs = ({ today, cid, sid, onClose }) => {
  const { t } = useTranslation();

  const { subjectAlreadySlotMarkList, subjectAlreadySlotMarkListRefetch } =
    useSubjectAlreadySlotMarkList({
      data: {
        cid: sid,
        // cid: cid,
        date: today,
      },
      skip: !sid,
    });

  useEffect(() => {
    if (sid) {
      subjectAlreadySlotMarkListRefetch();
    }
  }, [subjectAlreadySlotMarkListRefetch, today]);

  //   console.info("subjectAlreadySlotMarkList", subjectAlreadySlotMarkList);
  return (
    <>
      <PopupWrapper onClose={onClose}>
        <div className={style.add_academic_record}>
          <div className={style.add_academic_record_title}>
            <div className={style.add_academic_record_text}>
              <img
                src="/images/arrow-left-fees-icon.svg"
                onClick={onClose}
                alt="back icon"
                style={{
                  cursor: "pointer",
                }}
              />
              <h6>{t("already_mark_attendace")}</h6>
            </div>
          </div>
          <BorderBottom
            customStyle={{
              width: "100%",
              marginBottom: "0.7rem",
            }}
          />
          {subjectAlreadySlotMarkList?.slots?.map((st) => (
            <section className={style.st_card}>
              <div className={style.st_card_container}>
                <img
                  src={
                    st?.subject?.subjectTeacherName?.staffProfilePhoto
                      ? `${imageShowUrl}/${st?.subject?.subjectTeacherName?.staffProfilePhoto}`
                      : `${assestsFeedUrl}/feed-user-avatar.svg`
                  }
                  alt="staff icon"
                />
                <div className={style.stcc_inner}>
                  <h6>{`${
                    st?.subject?.subjectTeacherName?.staffFirstName ?? ""
                  } ${st?.subject?.subjectTeacherName?.staffMiddleName ?? ""} ${
                    st?.subject?.subjectTeacherName?.staffLastName ?? ""
                  }`}</h6>
                  <p>
                    {t("log_subject")} {": "}
                    <span
                      style={{
                        fontWeight: "500",
                      }}
                    >
                      {st?.subject?.subjectName ?? ""}
                    </span>
                  </p>
                  <p>
                    {t("from")} {": "}
                    {st?.from}, {t("to")} {st?.to}
                  </p>
                </div>
              </div>
            </section>
          ))}

          {subjectAlreadySlotMarkList?.slots?.length === 0 ? (
            <EmptyMessage title="No marked attendance list" />
          ) : null}
        </div>
      </PopupWrapper>
    </>
  );
};

export default AttendaceTimeSlotLogs;
