import React, { useState } from "react";
import { useTranslation } from "react-i18next";
import JoinFormInput from "../../../../../../JoiningForm/Student/Form/JoinFormInput";
import {
  useSubjectAlreadyAttendaceUpdateTimeSlot,
  useSubjectAttendaceUpdateTimeSlot,
} from "../../../../../../hooks/member_tab/subject-api";
import QLoader from "../../../../../../Loader/QLoader";
import { Notfication } from "../../../../../../validation/Snackbar";
import AttendaceTimeSlotLogs from "./AttendaceTimeSlotLogs";
import AttendaceUndo from "./AttendaceUndo";
import CustomCreateButton from "../../../../../../optimized/Ui/Button/CustomCreateButton";
import { assestsUtilityUrl } from "../../../../../../optimized/Services/UrlConfig/AssestsBaseUrl";

const AttendaceTimeSlot = ({
  today,
  setState,
  state,
  sid,
  cid,
  // onClosePopup,
  flow,
  whichLecture,
  alreadyMark,
  onRefetch,
  children,
}) => {
  const { t } = useTranslation();
  // const navigate = useNavigate();
  const [disabled, setDisabled] = useState(false);
  const [logs, setLogs] = useState(false);
  const [undo, setUndo] = useState(false);
  const [notificationState, setNotificationState] = useState({
    msg: "",
    run: false,
  });
  const [subjectAttendaceUpdateTimeSlot] = useSubjectAttendaceUpdateTimeSlot();
  const [subjectAlreadyAttendaceUpdateTimeSlot] =
    useSubjectAlreadyAttendaceUpdateTimeSlot();

  const dt = today.split("-");
  const d = dt[0] > 9 ? dt[0] : `0${dt[0]}`;
  const mt = dt[1] > 9 ? dt[1] : `0${dt[1]}`;
  // const onClose = () => {
  //   navigate(-1);
  // };
  const onChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };
  const onUpdate = () => {
    if (state?.from && state?.to && flow) {
      setDisabled((pre) => !pre);
      const dates = today.split("-");
      const day = dates[0] > 9 ? dates[0] : `0${dates[0]}`;
      const month = dates[1] > 9 ? dates[1] : `0${dates[1]}`;
      if (alreadyMark?._id) {
        subjectAlreadyAttendaceUpdateTimeSlot({
          sid: sid,
          timeSlot: {
            date: `${day}/${month}/${dates[2]}`,
            from: timeFormatTable(state?.from),
            to: timeFormatTable(state?.to),
            cid: cid ? cid : sid,
            flow: flow,
            which_lecture: whichLecture,
            said: alreadyMark?._id,
          },
        })
          .then((res) => {
            setDisabled((pre) => !pre);
            if (res.data?.already_slot_mark) {
              setNotificationState({
                msg: `For this time slot attendace has been already marked by ${
                  res.data?.subject?.subjectName
                } - ${`${
                  res.data?.subject?.subjectTeacherName?.staffFirstName ?? ""
                } ${
                  res.data?.subject?.subjectTeacherName?.staffMiddleName ?? ""
                } ${
                  res.data?.subject?.subjectTeacherName?.staffLastName ?? ""
                }`}`,
                run: true,
              });
            } else {
              setState((prev) => ({
                ...prev,
                is_updated: true,
                is_timetable: false,
              }));
            }
          })
          .catch({});
      } else {
        subjectAttendaceUpdateTimeSlot({
          sid: sid,
          timeSlot: {
            date: `${day}/${month}/${dates[2]}`,
            from: timeFormatTable(state?.from),
            to: timeFormatTable(state?.to),
            cid: cid,
            flow: flow,
            which_lecture: whichLecture,
          },
        })
          .then((res) => {
            setDisabled((pre) => !pre);
            if (res.data?.already_slot_mark) {
              setNotificationState({
                msg: `For this time slot attendace has been already marked by ${
                  res.data?.subject?.subjectName
                } - ${`${
                  res.data?.subject?.subjectTeacherName?.staffFirstName ?? ""
                } ${
                  res.data?.subject?.subjectTeacherName?.staffMiddleName ?? ""
                } ${
                  res.data?.subject?.subjectTeacherName?.staffLastName ?? ""
                }`}`,
                run: true,
              });
            } else {
              setState((prev) => ({
                ...prev,
                is_updated: true,
                is_timetable: false,
              }));
            }
          })
          .catch({});
      }
    }
  };

  // console.info("state", state);
  return (
    <>
      <div
        style={{
          display: "flex",
          alignItems: "center",
          justifyContent: "space-between",
          gap: "1rem",
          padding: "1rem",
          width: "100%",
        }}
      >
        <JoinFormInput
          labelText={t("from")}
          placeholder="00:00"
          name={"from"}
          value={get24Format(state?.from)}
          type={"time"}
          onChange={onChange}
          customStyleContainer={{
            display: "flex",
            justifyContent: "flex-start",
            alignItems: "center",
            gap: "1rem",
            width: "fit-content",
          }}
        />
        <JoinFormInput
          labelText={t("to")}
          placeholder="00:00"
          name={"to"}
          value={get24Format(state?.to)}
          type={"time"}
          onChange={onChange}
          customStyleContainer={{
            display: "flex",
            justifyContent: "flex-start",
            alignItems: "center",
            gap: "1rem",
            width: "fit-content",
          }}
        />
        <div
          style={{
            display: "flex",
            alignItems: "center",
            justifyContent: "space-between",
            gap: "1rem",
          }}
        >
          <CustomCreateButton
            label={t("update_time")}
            onAction={onUpdate}
            customStyle={{
              "--ccb-bg": "#1C023B",
              "--ccb-b": "#1C023B",
              "--ccb-text": "#ffffff",
              width: "fit-content",
              fontWeight: "400",
              fontSize: "14px",
            }}
          />

          {children}

          <CustomCreateButton
            label={t("delete")}
            onAction={() => setUndo(true)}
            customStyle={{
              "--ccb-bg": "#9F0C08",
              "--ccb-b": "#9F0C08",
              "--ccb-text": "#ffffff",
              gap: "0.7rem",
              width: "fit-content",
              fontWeight: "400",
              fontSize: "14px",
            }}
          >
            <img src={`${assestsUtilityUrl}/delete.svg`} alt="delete icon" />
          </CustomCreateButton>
        </div>

        {/* <div
          className={style.time_slot_btn}
          onClick={() => setLogs(true)}
          style={{
            textAlign: "center",
          }}
        >
          {t("view_logs")}
        </div> */}
        {disabled && <QLoader />}
      </div>

      {logs && (
        <AttendaceTimeSlotLogs
          cid={cid}
          onClose={() => setLogs(false)}
          today={`${d}/${mt}/${dt[2]}`}
        />
      )}

      {undo && (
        <AttendaceUndo
          said={alreadyMark?._id}
          onClose={() => setUndo(false)}
          onRefetch={onRefetch}
          flow={flow}
        />
      )}

      {notificationState.run && (
        <Notfication
          msg={notificationState.msg}
          run={notificationState.run}
          setRun={() => setNotificationState({ msg: "", run: false })}
          postiton="bottom"
          type="error"
        />
      )}
    </>
  );
};

export default AttendaceTimeSlot;

const timeFormatTable = (vale) => {
  const splitValue = vale?.split(":");
  let meridian = "";
  if (splitValue[0] > 12) {
    meridian = "Pm";
    splitValue[0] -= 12;
  } else if (splitValue[0] < 12) {
    meridian = "Am";
    if (splitValue[0] === 0) {
      splitValue[0] = 12;
    }
  } else {
    meridian = "Pm";
  }
  const hour = splitValue[0] > 9 ? splitValue[0] : `0${+splitValue[0]}`;
  let res = `${hour}:${splitValue[1]} ${meridian}`;
  return res?.substring(0, 8);
};

const get24Format = (data) => {
  let splt = data?.split(" ");
  let nt = splt?.[0]?.split(":");
  let hr = "";

  if (splt?.[1] === "Pm") {
    if (+nt?.[0] === 12) {
      hr = nt?.[0];
    } else {
      hr = +nt?.[0] + 12;
    }
  } else {
    hr = nt?.[0];
  }
  return `${hr}:${nt?.[1]}`;
};
