import PopupWrapper from "../../../FinanceManager/FeesStructure/PopupWrapper";
import style from "../../Academics/Academics.module.css";
import { useTranslation } from "react-i18next";
import BorderBottom from "../../../../../../StaffTab/components/DividerTab/MainContent/StaffAProfile/BorderBottom";
import CreateButton from "../../../../../../optimized/Ui/Button/CreateButton";
import QLoader from "../../../../../../Loader/QLoader";
import { useSubjectAttendaceRemove } from "../../../../../../hooks/member_tab/subject-api";
import { useState } from "react";

const AttendaceUndo = ({ said, onClose, onRefetch, flow }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [subjectAttendaceRemove] = useSubjectAttendaceRemove();
  const onRemoveAttendance = () => {
    if (said && flow) {
      setDisabled((pre) => !pre);
      subjectAttendaceRemove({
        said: said,
        flow: flow,
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };
  return (
    <>
      {disabled ? (
        <QLoader />
      ) : (
        <PopupWrapper onClose={onClose}>
          <div
            className={style.add_academic_record}
            style={{
              minHeight: "12rem",
            }}
          >
            <div className={style.add_academic_record_title}>
              <div className={style.add_academic_record_text}>
                <img
                  src="/images/arrow-left-fees-icon.svg"
                  onClick={onClose}
                  alt="back icon"
                  style={{
                    cursor: "pointer",
                  }}
                />
                <h6>{t("delete_today_marked_attendance")}</h6>
              </div>
            </div>
            <BorderBottom
              customStyle={{
                width: "100%",
                marginBottom: "0.7rem",
              }}
            />
            <CreateButton
              label={t("confirm")}
              onAction={onRemoveAttendance}
              customStyle={{
                marginTop: "4rem",
              }}
            />
          </div>
        </PopupWrapper>
      )}
    </>
  );
};

export default AttendaceUndo;
