import { useEffect, useState } from "react";
import { useClassAllStudent } from "../../../../hooks/member_tab/class-api";
import SelectSubjectCatalogCard from "./SelectSubjectCatalogCard";
import Modal from "../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import CreateButton from "../../../Ui/Button/CreateButton";
import {
  assestsNavbarUrl,
  assestsUserFinanceUrl,
} from "../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import QvipleLoader from "../../../Loader/QvipleLoader";
import Loading from "../../../Loader/Loading";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import style from "../../PlateformUser/PlateformUser.module.css";
import { useTranslation } from "react-i18next";

const SelectSubjectCatalog = ({
  title,
  onClose,
  selectedId,
  onSelectStudent,
  cid,
  isMultiSelect,
  onMultiSelect,
  selectList,
  onDone,
  selectLabel,
  disabledId,
  setExtra,
}) => {
  const { t } = useTranslation();
  const [students, setStudents] = useState([]);
  const [studentCopy, setStudentCopy] = useState([]);
  const [search, setSearch] = useState("");
  const [selectAll, setSelectAll] = useState(false);
  const { classAllStudent, classAllStudentRefetch, classAllStudentLoading } =
    useClassAllStudent({
      data: {
        cid: cid,
        page: 1,
        limit: 3000,
      },
      skip: !cid,
    });
  useEffect(() => {
    if (cid) classAllStudentRefetch();
  }, [cid, classAllStudentRefetch]);

  useEffect(() => {
    if (classAllStudent?.classes?.ApproveStudent) {
      setStudents(classAllStudent?.classes?.ApproveStudent);
      let arr = [];
      for (let dt of classAllStudent?.classes?.ApproveStudent) {
        if (!disabledId?.includes(dt?._id)) {
          arr.push(dt);
        }
      }
      setStudents(arr);
      setStudentCopy(arr);
    }
  }, [classAllStudent?.classes?.ApproveStudent, disabledId]);
  const onSearchStudent = (e) => {
    if (e.target.value) {
      setSearch(e.target.value);
      const filterStudent = studentCopy?.filter((student) => {
        if (
          `${student?.studentFirstName} ${
            student?.studentMiddleName ? student?.studentMiddleName + " " : ""
          }${student?.studentLastName}`
            ?.toLowerCase()
            ?.includes(e.target.value?.toLowerCase())
        ) {
          return student;
        } else {
          return null;
        }
      });
      setStudents(filterStudent);
    } else {
      setSearch("");
      setStudents(studentCopy);
    }
  };

  const onSelect = (value) => {
    if (selectedId === value?._id) {
    } else {
      onSelectStudent(value);
    }
  };
  const onSelectAll = () => {
    setSelectAll(true);
    let arr = [];
    for (let dt of students) {
      arr.push(dt?._id);
    }
    setExtra((pre) => ({
      ...pre,
      students: arr,
    }));
  };

  const onRemove = () => {
    setSelectAll(false);
    setExtra((pre) => ({
      ...pre,
      students: [],
    }));
  };

  return (
    <>
      <Modal onClose={onClose}>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>{title ?? t("student_list")}</h6>
              </div>
              <div className={style.modal_container_btn_container}>
                {selectAll ? (
                  <CreateButton
                    label={t("remove")}
                    onAction={onRemove}
                    customStyle={{
                      margin: "0",
                      padding: "0.3rem 0.5rem",
                    }}
                  />
                ) : (
                  <CreateButton
                    label={t("select_all")}
                    count={selectList?.length}
                    onAction={onSelectAll}
                    customStyle={{
                      margin: "0",
                      padding: "0.3rem 0.5rem",
                    }}
                  />
                )}
                {isMultiSelect && (
                  <CreateButton
                    label={t(selectLabel ?? "select")}
                    count={selectList?.length}
                    onAction={onDone}
                    customStyle={{
                      margin: "0",
                      padding: "0.3rem 0.5rem",
                    }}
                  />
                )}
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={onClose}
                />
              </div>
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingBottom: "1rem",
            }}
          >
            <div
              className={style.assign_search_container}
              style={{
                marginBottom: "1rem",
              }}
            >
              <input
                type="text"
                placeholder={t("search")}
                className={style.assign_search_input}
                onChange={onSearchStudent}
                style={{
                  width: "100%",
                }}
              />
              <img
                src={`${assestsNavbarUrl}/navbar-search.svg`}
                alt="search icon"
              />
            </div>
            {students?.map((student) => (
              <SelectSubjectCatalogCard
                key={student?._id}
                student={student}
                selectedId={selectedId}
                onSelect={onSelect}
                selectList={selectList}
                isMultiSelect={isMultiSelect}
                onMultiSelect={onMultiSelect}
              />
            ))}

            {classAllStudentLoading ? (
              search ? (
                <QvipleLoader />
              ) : (
                <Loading />
              )
            ) : search ? (
              classAllStudent?.classes?.ApproveStudent?.length === 0 && (
                <EmptyMessage title="No students found related this search." />
              )
            ) : (
              classAllStudent?.classes?.ApproveStudent?.length === 0 && (
                <EmptyMessage title="No students." />
              )
            )}
          </UserMemberContentWrapper>
        </div>
      </Modal>
    </>
  );
};

export default SelectSubjectCatalog;
