import React, { useEffect, useRef, useState } from "react";
import { imageShowUrl } from "../../../../../services/BaseUrl";
import style from "../../ClassTeacher/ClassCatalog/Catalog/Catalog.module.css";
import { useTranslation } from "react-i18next";
import { Link, useParams } from "react-router-dom";
import { useGetAllStudentOneSubject } from "../../../../../hooks/member_tab/subject-api";
import CreateButton from "../../../../../optimized/Ui/Button/CreateButton";
import { assestsUserFinanceUrl } from "../../../../../optimized/Services/UrlConfig/AssestsBaseUrl";
import { useSubjectAddCatalogStudent } from "../../../../../optimized/Hooks/ApiHooks/SubjectManager/subject-manager-api";
import SelectSubjectCatalog from "../../../../../optimized/UserStaffMember/SubjectTeacher/SubjectCatalog/SelectSubjectCatalog";
import SubjectCatalogMenu from "./SubjectCatalogMenu";
import QvipleLoader from "../../../../../optimized/Loader/QvipleLoader";
import InstituteDepartmentAddStudent from "../../../../../optimized/InstituteMember/InstituteAcademicDepartment/InstituteDepartmentClass/InstituteDepartmentAddStudent/InstituteDepartmentAddStudent";
import InstituteDepartmentAddDseStudent from "../../../../../optimized/InstituteMember/InstituteAcademicDepartment/InstituteDepartmentClass/InstituteDepartmentAddStudent/InstituteDepartmentAddDseStudent";
import { useAddStudentInTheoryClasses } from "../../../../../optimized/Hooks/ApiHooks/AcademicDepartmentManager/academic-department-manager-api";

function SubjectAllStudent({
  cid,
  sid,
  isDepartment,
  cmid,
  academicDepartmentId,
}) {
  const dDate = new Date();
  const dates = dDate.toISOString()?.split("-");
  const day = dates[2]?.substring(0, 2);
  const month = dates[1];
  const params = useParams();
  const { t } = useTranslation();
  const [students, setStudents] = useState([]);
  const [open, setOpen] = useState("");
  const [step, setStep] = useState("");
  const [extra, setExtra] = useState({
    students: [],
    existingStudent: [],
  });
  const [disabled, setDisabled] = useState(false);

  const [subjectAddCatalogStudent] = useSubjectAddCatalogStudent();
  const [addStudentInTheoryClasses] = useAddStudentInTheoryClasses();

  const onToggle = (e, val) => {
    e.preventDefault();
    setOpen(val);
  };
  const onToggleClose = () => {
    setOpen("");
  };
  const onCloseStep = () => {
    setStep("");
    setExtra((prev) => ({
      ...prev,
      students: [],
    }));
  };
  // // for drag evenet
  // const dragItem = useRef();
  // const dragOverItem = useRef();

  const { getAllStudentOneSubject, getAllStudentOneSubjectRefetch } =
    useGetAllStudentOneSubject({
      data: {
        cid: cid ? cid : sid,
        subjectId: sid,
        today: `${day}/${month}/${dates[0]}`,
      },
      skip: cid ? !cid : !sid,
    });

  useEffect(() => {
    if (cid) getAllStudentOneSubjectRefetch();
  }, [cid, getAllStudentOneSubjectRefetch]);

  useEffect(() => {
    if (getAllStudentOneSubject?.classes?.ApproveStudent) {
      setStudents(getAllStudentOneSubject?.classes?.ApproveStudent);
      if (getAllStudentOneSubject?.classes?.which_dataset === "Subject") {
        let arr = [];
        for (let dt of getAllStudentOneSubject?.classes?.ApproveStudent) {
          arr.push(dt?._id);
        }
        setExtra((pre) => ({
          ...pre,
          existingStudent: arr,
        }));
      }
    }

    if (getAllStudentOneSubject?.classes?.which_dataset === "Classes") {
      setExtra((pre) => ({
        ...pre,
        existingStudent: [],
      }));
    }
  }, [
    getAllStudentOneSubject?.classes?.ApproveStudent,
    getAllStudentOneSubject?.classes?.which_dataset,
  ]);

  const onSearchStudent = (e) => {
    if (e.target.value) {
      const filterStudent =
        getAllStudentOneSubject?.classes?.ApproveStudent?.filter((student) => {
          if (
            `${student?.studentFirstName} ${
              student?.studentMiddleName ? student?.studentMiddleName + " " : ""
            }${student?.studentLastName}`
              ?.toLowerCase()
              ?.includes(e.target.value?.toLowerCase())
          ) {
            return student;
          } else {
            return null;
          }
        });
      setStudents(filterStudent);
    } else {
      setStudents(getAllStudentOneSubject?.classes?.ApproveStudent);
    }
  };

  const onSelectStudent = (val) => {
    if (extra?.students?.includes(val?._id)) {
      setExtra((pre) => ({
        ...pre,
        students: pre?.students?.filter((id) => id !== val?._id),
      }));
    } else {
      setExtra((pre) => ({
        ...pre,
        students: [...pre?.students, val?._id],
      }));
    }
  };

  const onAdd = () => {
    if (sid && extra.students?.length > 0) {
      setStep("");
      setDisabled((pre) => !pre);
      subjectAddCatalogStudent({
        sid: sid,
        bodyParameter: {
          student_list: extra.students,
        },
      })
        .then(() => {
          getAllStudentOneSubjectRefetch();
          onCloseStep();
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  const onAddStudent = (data) => {
    if (data?.length > 0 && sid) {
      setDisabled((pre) => !pre);
      addStudentInTheoryClasses({
        sid: sid,
        bodyParameter: {
          students: data,
        },
      })
        .then(() => {
          getAllStudentOneSubjectRefetch();
          onCloseStep();
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  const onAddToggle = () => {
    if (cmid) {
      setStep(2);
    } else {
      setStep(1);
    }
  };
  // const onDragEventStart = (e, position) => {
  //   e.stopPropagation();
  //   dragItem.current = position;
  // };

  // const onDragEventEnter = (e, position) => {
  //   e.stopPropagation();
  //   dragOverItem.current = position;
  // };

  // const onDropItem = (e) => {
  //   e.stopPropagation();
  //   //for only ids
  //   const studentCopy = [...students];
  //   const dragItemContentCls = studentCopy[dragItem.current];
  //   studentCopy.splice(dragItem.current, 1);
  //   studentCopy.splice(dragOverItem.current, 0, dragItemContentCls);

  //   dragItem.current = null;
  //   dragOverItem.current = null;
  //   setStudents(studentCopy);
  // };
  return (
    <>
      <div
        className={style.catalog}
        style={{
          border: isDepartment ? "none" : "0.5px solid #cacaca",
        }}
      >
        <div className={style.searchContainerr}>
          <div className={style.searchContainer}>
            <img alt="" src="/images/search-dept-icon.svg" />
            <input
              type="text"
              placeholder={t("search-label")}
              onChange={onSearchStudent}
            />
          </div>
          {cmid ? (
            <>
              <CreateButton
                label="add_student"
                customStyle={{
                  margin: "0",
                  padding: "0.4rem 1rem",
                  minWidth: "fit-content",
                }}
                onAction={onAddToggle}
              />
              <CreateButton
                label={t("add_dse_student")}
                customStyle={{
                  margin: "0",
                  padding: "0.4rem 1rem",
                  minWidth: "fit-content",
                }}
                onAction={() => setStep(3)}
              />
            </>
          ) : (
            <CreateButton
              label="add_student"
              customStyle={{
                paddingBlock: "0.4rem",
                margin: "0",
              }}
              onAction={onAddToggle}
            />
          )}

          {/* <div className={style.checklistbtn}>Settings</div> */}
        </div>

        <div className={style.itemsContainerr}>
          {students?.map((stu, index) => (
            <Link
              to={`/q/${params.username}/student/profile`}
              state={{ sid: stu?._id, viewAs: "SUBJECT_TEACHER" }}
            >
              <div
                className={style.itemsContaiiner}
                // draggable
                // onDragStart={(e) => onDragEventStart(e, index)}
                // onDragEnter={(e) => onDragEventEnter(e, index)}
                // onDragEnd={onDropItem}
              >
                <div className={style.item}>
                  <p>{stu?.studentROLLNO}</p>
                  <img
                    alt=""
                    src={
                      stu?.studentProfilePhoto
                        ? `${imageShowUrl}/${stu?.studentProfilePhoto}`
                        : "/images/member_tab/class/default_avatar.svg"
                    }
                  />
                  <div className={style.student_catalog_with_attendance}>
                    <div className={style.student_catalog_name}>
                      <h5>
                        {stu?.studentFirstName ?? ""}{" "}
                        {stu?.studentMiddleName ?? " "}{" "}
                        {stu?.studentLastName ?? ""}
                      </h5>
                      <h6>
                        {t("gr_number")}{" "}
                        <span
                          style={{
                            fontWeight: "500",
                          }}
                        >
                          {stu?.studentGRNO ?? ""}
                        </span>
                        {stu?.student_prn_enroll_number ? (
                          <span
                            style={{
                              fontWeight: "500",
                            }}
                          >
                            {`, ${t("enrollment_number_only")}: ${
                              stu?.student_prn_enroll_number
                            }`}
                          </span>
                        ) : null}
                      </h6>
                    </div>
                    <div
                      className={style.student_catalog_name}
                      style={{
                        alignItems: "center",
                        flexDirection: "row",
                      }}
                    >
                      <div
                        className={style.student_catalog_name}
                        style={{
                          alignItems: "flex-end",
                        }}
                      >
                        <h6>
                          {t("today")}
                          {" : "}
                          <span
                            style={{
                              fontWeight: "500",
                              color:
                                stu?.todayStatus === "P"
                                  ? "#20b038"
                                  : "#ee3833",
                            }}
                          >
                            {stu?.todayStatus ?? ""}
                          </span>
                        </h6>
                        <h6>
                          {t("overall_percentage")}
                          {" : "}
                          <span
                            style={{
                              fontWeight: "500",
                              color:
                                stu?.totalPercentage > 75
                                  ? "#20b038"
                                  : "#ee3833",
                            }}
                          >
                            {stu?.totalPercentage ?? ""}
                          </span>
                        </h6>
                      </div>
                      <img
                        src={`${assestsUserFinanceUrl}/menu.svg`}
                        alt="menu ictem"
                        onClick={(e) => onToggle(e, stu)}
                      />
                    </div>
                  </div>
                </div>
              </div>
            </Link>
          ))}
        </div>
      </div>
      {step === 1 ? (
        <SelectSubjectCatalog
          onClose={onCloseStep}
          cid={cid}
          isMultiSelect
          onMultiSelect={onSelectStudent}
          onDone={onAdd}
          selectList={extra.students}
          disabledId={extra.existingStudent}
          selectLabel="add_student"
          setExtra={setExtra}
        />
      ) : step === 2 ? (
        <InstituteDepartmentAddStudent
          onClose={onCloseStep}
          clsId={cmid}
          onAdd={onAddStudent}
          academicDepartmentId={academicDepartmentId}
        />
      ) : step === 3 ? (
        <InstituteDepartmentAddDseStudent
          onClose={onCloseStep}
          clsId={cmid}
          onAdd={onAddStudent}
          academicDepartmentId={academicDepartmentId}
        />
      ) : null}
      {open && (
        <SubjectCatalogMenu
          sid={sid}
          onClose={onToggleClose}
          menuData={open}
          onRefetch={getAllStudentOneSubjectRefetch}
        />
      )}
      {disabled && <QvipleLoader />}
    </>
  );
}

export default SubjectAllStudent;
